import { Component, OnInit } from '@angular/core';
import { GaTicketingService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService } from 'ng-zorro-antd';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { Ticketing } from 'src/app/sdk/asset/models';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private gaTicketingService: GaTicketingService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
        private pegawaiDetailService: PegawaiDetailService,
    ) {}

    listData: Ticketing[] = []


    id_pegawai_biodata: string = ''
    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getKeluarga()
    }

    getKeluarga() {
        this.spinner.show()
        this.gaTicketingService
            .getGaTicketings(JSON.stringify({ id_pegawai: this.id_pegawai }))
            .subscribe(res => {
                this.listData = res.data
                this.spinner.hide()
            })
    }

    delete(id) {
        this.spinner.show()
        this.gaTicketingService
            .deleteGaTicketingsId(id)
            .subscribe(data => {
                this.getKeluarga()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }
}
